body{
  margin:0%;
  box-sizing: border-box;
  min-width: 1140px; 
  font-family: 'Mukta', sans-serif;
  overflow-x: hidden;
}

.App {
  text-align: center;
  margin: 0;
  display: block;
}

#home{
  padding:10%;
  color:white;
  background-color: #000000;
  background-image: url("https://www.transparenttextures.com/patterns/bright-squares.png");
}

#home h1{
  font-family: 'Anton', sans-serif;
  color:white;
  font-size: 5rem;
}
#home span{
  color:gold;
}
#intro-section{
  background:#333;
  padding:2% 5%;
}
#intro-section h1{
  font-family: 'Anton', sans-serif;
  font-size: 3.5rem;
  color:white;
}
#intro-section h1 span{
  color:gold;
}

.intro{
  display: flex;
  flex-direction: row;
  justify-content:space-evenly;
  margin:1% 5%;
}
.intro-text{
  width:50%;
  margin: auto;
  font-size:2.25rem;
  color:#fdfcdc;
}
.intro-img{
  margin: auto;
}
.intro-img img{
  height:400px;
  margin: auto;
}
#examples-section{
  background:#292929;
  padding: 10px;
}
.example-heading{
  color:#fff;
  font-size:3rem;
  font-family: 'Anton', sans-serif;
}
.example-heading span{
  color:gold;
}
.example-div{
  display: flex;
  flex-direction:row;
  justify-content: space-evenly;
  background:#292929;
  padding:20px 40px;
}
.example-card{
  width:28%;
  padding: 20px;
  text-align: center;
  background: #f2e3bc;
  border-radius:20px;
  border: 2px double #ff006e;
}

.example-card img{
  height:400px;
  max-width: 90%; 
  border-radius:10px; 
}
.example-card h6{
  font-size:1.75rem;
  color:#264653;
  width:90%;
  margin:auto;
}
.example-card p{
  width:90%;
  margin: auto;
  color:#2f3e46;
  font-size:1.5rem;
  padding:5px 0;
}

.face-example img{
  height:600px;
  background: #ff006e;
  border-radius: 30px;
  margin: auto;
}
.face-example h4{
  font-size:4.5rem;
  width:28%;
  text-align: left;
  color:#06d6a0;
}
.face-example div{
  width: 30%;
  font-size: 2.2rem;
  color:#fff;
  margin: auto 0;
}
.face-example div p{
  margin: 0;
  color:#bcf4de;
}

.interactive-face-div{
  margin:70px auto;
  background: white;
  color:black;
}
.interactive-face-div h4{
  font-size:2rem;
}
.interactive-face-div button{
  font-size:1.5rem;
  background: #f77f00;
  border-radius:20px;
  padding:0 10px;
  cursor: pointer;
  transition: all ease .4s;
  outline: none;
}
.interactive-face-div button:hover{
  background: #fcbf49;
}

.logo-example-div{
  padding: 5%;
  display:flex;
  flex-direction: column;
  background: #60DBC7;
  padding:5px;
  
}
.logo-example-div img{
  height:250px;
  width:350px;
  object-fit:fill;
}
.logo-text{
  color:#e7d8c9;
  line-height: 1.25;
  text-align: left;
  width: 70%;
  margin:40px auto;
  font-size: 2rem;
}
.fact-bot{
  display:flexbox;
  background: #06d6a0;
}
.btn-div{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.btn{
  margin: auto;
  width: 370px;
  background: #f77f00;
  color: #fff;
  font-size: 1.5rem;
  padding: 10px;
  border-radius: 20px;
  cursor: pointer;
  transition: all ease .4s;
  outline: none;
}
.btn:hover{
  background: #fcbf49;
}
.mobile-heading{
  font-family: 'Anton', sans-serif;
  background: #06d6a0;
  padding:40px;
  margin-bottom: 0;
  margin-top:100px;
  font-size:3rem;
  color:#202249
}
.mobile-heading span{
  color:gold;
}
.mobile{
  height:600px;
  width:500px;
  background: #e5b600;
  border-top:40px solid black;
  border-left:10px solid black;
  border-right:10px solid black;
  border-bottom:30px solid black;
  border-radius: 10px;
  overflow-y: scroll;
}
.mobile nav{
  width: 100%;
  background: #fdc500;
  color:#fcfbd5;
  font-weight: 800;
  padding:20px 0;
  font-size:30px;
  font-family: 'Pacifico', cursive;
}
.user-message{
  text-align:center;
  font-size:1.5rem;
  background:#fff;
  padding:10px;
  margin-left:200px;
  margin-right: 0;
}
.robot-message{
  text-align:center;
  font-size:1.5rem;
  background:#fff;
  padding:10px;
  margin-right:200px;
  margin-left: 0;
}
.painting-grid{
  display: flexbox;
  /* grid-template-columns: auto auto ; */
  margin: auto;
}
.painting-grid img{
  height:400px;
}

.about-div{
  padding: 50px;
  border:10px solid gold;
  border-right: 5px solid gold;;
}
.about-div h1{
  color:#202249;
  font-size: 4rem;
  font-family: 'Anton';
}
.about-div h1 span{
  color:gold;
}
.about-div-txt{
  font-size:2rem;
}
.about-div-derivation{
  display: flex;
  flex-direction: row;
}
.about-div-derivation p{
  width: 60%;
  font-size: 1.75rem;
  margin: auto;
}
.about-div-derivation p span{
  color:black;
  font-weight:bold;
}
.about-div-derivation img{
  height: 250px;
  margin: auto;
}

.about-div-derivation div{
  width: 50%;
  margin:auto;
}
.article-card img{
  height:200px;
  width: 200px;
  object-fit: cover;
  border-radius: 100%;
  transition: ease all .4s;
}
.article-card img:hover{
 border:4px solid white;
}
.article-div{
  background: #292929;
  margin: 0 auto;
  padding:20px;
  padding-bottom: 50px;
}
.article-div h1{
  font-size:2.5rem;
  font-family: 'Anton';
  background:#292929;
  color:#60DBC7;
}
.footer-div{
  padding:10px;
  background: #2f3e46;
  color:wheat;
}
.icons i{
  color:white;
  font-size: 2.5rem;
  padding:10px;
}




/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #fccf00;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #fff;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #fccf00;
}

